:root {
  --add-blue-color: #00acdc;
}

body {
  text-transform: uppercase;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

* {
  box-sizing: border-box;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.logoImg {
  width: 100%;
  height: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  white-space: nowrap;
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__item:not(:first-of-type) {
  margin-left: 20px;
}

.nav__link:hover {
  color: var(--add-blue-color);
}

.is-active {
  color: var(--add-blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--add-blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

